import React from 'react'
import LogoImage from '../../assets/logo.png'

import './index.scss'

const NavBar = ({ isCity = false, cityImage = null, country = '일본', cityName = '오사카' }) => {
    return (
        <div 
            className='nav-header' 
            style={
                isCity === true ? {
                    backgroundColor: 'transparent', 
                    backgroundImage: `url(${cityImage})`, 
                    height: '160px', 
                    backgroundPosition: 'center', 
                    backgroundRepeat: 'no-repeat', 
                    backgroundSize: 'cover'
                } : null
            }
        >
            <div className='nav' style={isCity === true ? { backgroundColor: 'rgba(0, 0, 0, 0.3)' } : null}>
                <img 
                    className='hamburger'
                    src='data:image/png;base64,' 
                    alt='hamburger'
                />
                <img className='logo' src={LogoImage} alt='my-real-trip'/>
                <img 
                    className='search'
                    src='data:image/svg+xml;base64,' 
                    alt='search'
                />
            </div>
            { isCity === false && ( 
                <div className='categories'>
                    <ul>
                        <li>
                            <span>투어&amp;티켓</span>
                        </li>
                        <li>
                            <span>항공권</span>
                        </li>
                        <li>
                            <span>숙소</span>
                        </li>
                        <li>
                            <span>특가여행</span>
                        </li>
                        <li>
                            <span>여행준비</span>
                        </li>
                    </ul>
                </div>
            )}
            { isCity === true && (
                <div className='city-header'>
                    <p className='city-country'>{country}</p>
                    <h1 className='city-name'>{cityName}</h1>
                </div>
            )}
        </div>
    )
}

export default NavBar