import React from 'react'

import './index.scss'

const AdvertisementCard = ({ image, title, breadcrumb, cost }) => {
    return (
        <div className='advertisementBox'>
            <div className='imgArea'>
                <img src={image} alt='advertisement'/>
            </div>
            <div className='textArea'>
                <div className='broadcrumb'>
                    <span>{breadcrumb}</span>
                </div>
                <div className='title'>
                    <p>{title}</p>
                </div>
                <div className='cost'>
                    <span>{cost}</span>
                </div>
            </div>
        </div>
    )
}

export default AdvertisementCard